body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  top: 0;
}

main {
  margin-top: 80px;
}

pre[class*="language-cmd"] {
  width: 100%;
  background-color: #000000;
  color: lawngreen;
  padding: 20px;
  border-radius: 5px;
  overflow: auto;
  text-align: start;
  white-space: pre-wrap;
  box-shadow: 3px 3px 3px 3px rgb(198, 198, 198);
  transition: all .4s;
  &:hover{
      background-color: #242424;
      box-shadow: 3px 3px 3px 3px rgb(79, 78, 78);
  }
}
pre[class*="language-jsx"] {
  width: 100%;
  background-color: #000000;
  color: lawngreen;
  padding: 20px;
  border-radius: 5px;
  overflow: auto;
  white-space: pre-wrap;
  box-shadow: 3px 3px 3px 3px rgb(198, 198, 198);
}
pre[class*="language-javascript"] {
  width: 100%;
  background-color: #000000;
  color: lawngreen;
  padding: 20px;
  border-radius: 5px;
  overflow: auto;
  white-space: pre-wrap;
  box-shadow: 3px 3px 3px 3px rgb(198, 198, 198);
}

.token.keyword {
  color: #007bff;
  font-weight: bold;
}

.token.string {
  color: #28a745;
}

.token.comment {
  color: #888;
}

.hover-rotate {
  animation: rotate 1s ease-in-out infinite alternate;
  /* 회전 애니메이션 설정 */
}

@keyframes rotate {
  0% {
    transform: rotate(-2deg);
  }

  100% {
    transform: rotate(2deg);
  }
}

@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';
